import React from 'react';

import { Link } from 'react-router-dom';

import { Tiles } from '@daimler/react-ux-business-app';

import { DashboardWrapper, Title, CardContainer } from './styled';

import type { RoutesType } from '@router/types';
import { useTranslation } from '@i10n/index';

type DashboardDomainProps = {
  tileTitle: string;
  pages: RoutesType;
};

export const DashboardDomain: React.FC<DashboardDomainProps> = ({
  tileTitle,
  pages,
}) => {
  const t = useTranslation();
  return (
    <DashboardWrapper>
      <Title>{t(tileTitle)}</Title>
      <CardContainer>
        {pages.map(({ path, title, description }) => (
          <Link to={path} key={path}>
            <Tiles
              title={title as string}
              description={description as string}
            />
          </Link>
        ))}
      </CardContainer>
    </DashboardWrapper>
  );
};
