import { Permission } from '@permissions/constants';
import { RoutesType, TileGroup } from './types';

export const filterPagesByTileGroup = (routes: RoutesType, group: TileGroup) =>
  routes.filter(
    ({ tileGroup }) => tileGroup != undefined && [group].includes(tileGroup),
  );

export const filterPagesByPermissions = (
  routes: RoutesType,
  userPermissions: Permission[],
) =>
  routes.filter(
    ({ permissions }) =>
      permissions == undefined ||
      permissions.filter((p) => userPermissions.includes(p)).length > 0,
  );
