import React, { memo } from 'react';

import { DashboardDomain } from '@domains/dashboard';
import { filterPagesByTileGroup } from '@router/utils';

import { routes } from '../routes';
import { TileGroup } from '@router/types';
import { filterPagesByPermissions } from '@router/utils';
import { Permission } from '@permissions/constants';
import { usePermissions } from '@permissions/index';

function DashboardPage() {
  const userPermissions = usePermissions() as Permission[];
  const filteredRoutes = filterPagesByPermissions(routes, userPermissions);
  const pages = (tileGroup: TileGroup) =>
    filterPagesByTileGroup(filteredRoutes, tileGroup);

  return (
    <>
      {Object.values(TileGroup).map((tileGroup) => (
        <DashboardDomain
          key="dashboard-domain"
          tileTitle={`Rabs.Dashboard.TileGroup.${tileGroup}`}
          pages={pages(tileGroup)}
        />
      ))}
    </>
  );
}

export default memo(DashboardPage);
